function generateData(d) {
    let map = L.map('map').setView([40.41694, -3.70361], 6);

    L.tileLayer('https://tiles.stadiamaps.com/tiles/alidade_smooth_dark/{z}/{x}/{y}{r}.png?api_key=4571e300-9d19-4c0b-8c5e-e8d3377d85bf', {
        maxZoom: 19,
        attribution: '© OpenStreetMap | © StadiaMaps'
    }).addTo(map);

    for (let estacion in d['ListaEESSPrecio']) {
        //console.log(Object.keys(d['ListaEESSPrecio'][estacion]))

        let _lat = Number.parseFloat(d['ListaEESSPrecio'][estacion]['Latitud'].replace(',', '.'))
        let _lon = Number.parseFloat(d['ListaEESSPrecio'][estacion]['Longitud (WGS84)'].replace(',', '.'))

        let _gasA95 = d['ListaEESSPrecio'][estacion]['Precio Gasolina 95 E5']
        let _gasA98 = d['ListaEESSPrecio'][estacion]['Precio Gasolina 98 E5']
        let _gasA = d['ListaEESSPrecio'][estacion]['Precio Gasoleo A']

        let _direccion = d['ListaEESSPrecio'][estacion]['Dirección']

        let info = '<p> <b> Dirección </b> </p>'
        info += '<p>' + _direccion + '</p>'
        info += '<ul>'
        info += '<li> <b> Gasolina 95 E5: </b> ' + _gasA95 + '€/l </li>'
        info += '<li> <b> Gasolina 98 E5: </b> ' + _gasA98 + '€/l </li>'
        info += '<li> <b> Gasoleo A: </b> ' + _gasA + '€/l </li>'
        info += '</ul>'

        let point = L.circleMarker([_lat, _lon], {
            radius: 2,
            border: 2,
            opacity: 0,
            fillOpacity: 0.5,
            color: '#144696',
            fillColor: '#0096c8'
        }).addTo(map).bindPopup(info);
    }
}

async function funcRequest(url) {
    await fetch(url)
        .then((response) => {
            return response.json(); // data into json
        })
        .then((data) => {
            generateData(data)
        })
        .catch(function (error) {
            console.log(error);
        });

}

const url = 'https://sedeaplicaciones.minetur.gob.es/ServiciosRESTCarburantes/PreciosCarburantes/EstacionesTerrestres/';

funcRequest(url);